<template>
  <div class="page-title-container">
    <!-- <div class="editor-options">
      <button class="btn btn-default mr-2"><i class="fas fa-heading"></i></button>
      <button class="btn btn-default mr-2"><i class="fas fa-bold"></i></button>
    </div> -->
    <div class="cover-page-container overflow-y-scroll h-100vh">
      <div class="cover-page">
          <p type="text" class="form-control text-center book_title mb-2" placeholder="Book title" @blur="updateTitle" :value="book_title" contenteditable="true">{{book_title}}</p>
          <p type="text" class="form-control text-center author_name" placeholder="Author Name" @blur="updateAuthor" :value="author_name" contenteditable="true">{{author_name}}</p>

          <input type="file" name="cover_image" id="cover_image" ref="cover_image" @change="updateCoverImage">
          <div v-if="cover_image === null" class="choose_image" title="Upload Image" @click="$refs.cover_image.click()">
            <p><i class="fas fa-3x fa-file-image"></i></p>
            <p>Upload a Cover Image</p>
          </div>
          <div v-else class="preview-image text-center">
            <i class="fas fa-2x fa-times-circle" @click="updateCoverImage"></i>
            <img :src="cover_image" alt="" :class="{'d-none': cover_image === null}" class="cover_image">
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      book_title: (state) => state.data.book_title,
      author_name:(state) => state.data.author_name,
      cover_image:(state) => state.data.cover_image,
    })
  },
  methods: {
    updateTitle(e){
      this.$store.commit('updateTitle', {book_title: e.target.innerText })
    },
    updateAuthor(e){
      this.$store.commit('updateAuthor', {author_name: e.target.innerText })
    },
    updateCoverImage(e){
      // console.log(e.target.files);
      this.$store.commit('updateCoverImage', {cover_image: e.target.files !== undefined ? e.target.files : [] })
    }
  }
}
</script>

<style lang="scss" scoped>
  
  .form-control {
    border-width: 0;

    &.book_title {
      font-size: 48px;
    }
    &.author_name {
      font-size: 24px;
    }
  }

</style>